<template>
  <div>
<div id="layout" class="d-flex flex-column">

        <main class="login_main" >
            <div class="content_wrapper" style="height: 100vH;">
                <div class="login_wrapper col col-sm-8 col-md-6  col-lg-5 col-xl-4 container-fluid container-sm">
                    <form class="login_form col">
                        <div class="logo"></div>

                        <label class="">環檢所 巡檢作業</label>
                        <input type="text" class="form-control" placeholder="Username" v-model="user.username">
                        <input type="password" class="form-control" placeholder="Password" v-model="user.password">
                        <button id="btn1" class="btn btn-primary btn-block" @click.prevent="login">登入</button>
                        <div class="error-message alert text-center" style="position: static;margin-bottom: 0;" :class="{'hidden': forget}">
                          帳號或密碼輸入錯誤
                        </div>
                        <!-- <div class="loading"></div>預設隱藏 讀取顯示 -->
                        <!-- <div class="p-t-90 font-weight-bold"><a href="#">忘記密碼?</a></div> -->
                    </form>
                </div>
            </div>
        </main>
    </div>
  </div>
</template>
<script>
import { mapGetters, mapActions } from 'vuex';
export default {
  data() {
    return {
      user:{
        username:'',
        password:''
      },
      forget:true,
    }
  },
  methods: {
    login () {
      const vm = this;
      vm.forget = true;
      const loginapi = 'https://www.sinetics.com.tw/ITDMLIMSAPI/System/Login'
      this.$http.post(loginapi, vm.user).then((response) => {
        vm.getcurrentuser(response.data);
        vm.$router.push('/SelMenu')
      }).catch((error) => {
        vm.forget = false;
      })
    },
    ...mapActions(['getcurrentuser']),
  },
  watch: {
    forget: function () {
      const vm = this;
      setTimeout(() => {
        vm.forget = true;
      }, 3000);
    }
  }
}
</script>